  import payInfo from '../../components/payInfo.vue'
  import urls from '../../../utils/urls.js'
  export default {
    components: {
      payInfo
    },
    data () {
      return {
        money: 10,
        bonus: 1,
        type: 'wx',
        showPayPop: false,
        wx_account: '',
        name: '',
        zfb_phone: '',
        bank: '',
        sub_branch: '',
        bank_name: '',
        card: '',
				isLogin: this.$store.state.isLogin
      }
    },
    onLoad (options) {
      this.bonus = options.bonus
    },
    methods: {
      changeStyle: function (e) {
        this.type = e.detail.value
      },
			t_money: function(e){
				this.money = e.detail.value
			},
      closePop: function (val) {
        this.showPayPop = val
      },
      getInfo: function (obj) {
        if (this.type === 'wx') {
          this.wx_account = obj
        } else if (this.type === 'zfb') {
          this.name = obj.name
          this.zfb_phone = obj.zfb_phone
        } else {
          this.bank = obj.bank
          this.sub_branch = obj.sub_branch
          this.bank_name = obj.bank_name
          this.card = obj.card
        }
      },
      sure: function () {
        // let uid = uni.getStorageSync('user').id
				// let uid = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        let obj = {}
        obj.uid = id
        obj.pay_type = this.type
        obj.bonus = this.money
        if (!this.money) {
          uni.showToast({
            title: '请填写提现金额',
            icon: 'none'
          })
        } else if (this.type === 'wx') {
          if (!this.wx_account) {
            this.showPayPop = true
          } else {
            obj.wx_account = this.wx_account
            uni.request({
              url: urls.HTTP + '/Generalize/cashWithdrawal',
              method: 'POST',
              data: obj,
              success: (res) => {
                // console.log('提交', res)
                if (res.data.code === 0) {
                  uni.showToast({
                    title: res.data.error,
                    icon: 'none'
                  })
                  this.bonus = res.data.userbonus
                } else {
                  uni.showToast({
                    title: res.data.error,
                    icon: 'none'
                  })
                }
              }
            })
          }
        } else if (this.type === 'zfb') {
          if (!this.name || !this.zfb_phone) {
            this.showPayPop = true
          } else {
            obj.name = this.name
            obj.zfb_phone = this.zfb_phone
            uni.request({
              url: urls.HTTP + '/Generalize/cashWithdrawal',
              method: 'POST',
              data: obj,
              success: (res) => {
                // console.log('提交', res)
                if (res.data.code === 0) {
                  uni.showToast({
                    title: res.data.error,
                    icon: 'none'
                  })
                  this.bonus = res.data.userbonus
                } else {
                  uni.showToast({
                    title: res.data.error,
                    icon: 'none'
                  })
                }
              }
            })
          }
        } else if (this.type === 'yl') {
          if (!this.bank || !this.sub_branch || !this.bank_name || !this.card) {
            this.showPayPop = true
          } else {
            obj.bank = this.bank
            obj.sub_branch = this.sub_branch
            obj.bank_name = this.bank_name
            obj.card = this.card
            uni.request({
              url: urls.HTTP + '/Generalize/cashWithdrawal',
              method: 'POST',
              data: obj,
              success: (res) => {
                // console.log('提交', res)
                if (res.data.code === 0) {
                  uni.showToast({
                    title: res.data.error,
                    icon: 'none'
                  })
                  this.bonus = res.data.userbonus
                } else {
                  uni.showToast({
                    title: res.data.error,
                    icon: 'none'
                  })
                }
              }
            })
          }
        }
      }
    }
  }
